<template>
  <div>
    <el-container>
      <el-header class="content-vaglin">
        运动员X档案生成器
      </el-header>
      <el-main>
    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px" class="demo-ruleForm">
  <el-form-item label="姓名" prop="name">
    <el-input v-model="ruleForm.name"></el-input>
  </el-form-item>
  <el-form-item label="图片地址" prop="url">
    <el-input v-model="ruleForm.url"></el-input>
  </el-form-item>
  <tr><td>
  <el-form-item label="身高" prop="height">
    <el-select v-model="ruleForm.height" placeholder="请选择身高" style="width:100%"  filterable>
      <el-option v-for="item in zheight" :label="item + 'cm'" :value="item"></el-option>
    </el-select>
  </el-form-item>
</td><td>
  <el-form-item label="体重" prop="weight">
    <el-select v-model="ruleForm.weight" placeholder="请选择体重" style="width:100%"  filterable>
      <el-option v-for="item in zweight" :label="item + 'kg'" :value="item"></el-option>
    </el-select>
  </el-form-item>
</td></tr>
<tr><td>
  <el-form-item label="胸围" prop="chest">
    <el-select v-model="ruleForm.chest" placeholder="请选择胸围" style="width:100%"  filterable>
      <el-option v-for="item in chest" :label="item + 'cm'" :value="item"></el-option>
    </el-select>
  </el-form-item>
</td><td>
  <el-form-item label="肩宽" prop="shoulder">
    <el-select v-model="ruleForm.shoulder" placeholder="请选择肩宽" style="width:100%"  filterable>
      <el-option v-for="item in shoulder" :label="item + 'cm'" :value="item"></el-option>
    </el-select>
  </el-form-item>
  </td></tr>
<tr><td>
  <el-form-item label="臂围" prop="biwei">
    <el-select v-model="ruleForm.biwei" placeholder="请选择臂围" style="width:100%"  filterable>
      <el-option v-for="item in biwei" :label="item + 'cm'" :value="item"></el-option>
    </el-select>
  </el-form-item>
</td><td>
  <el-form-item label="腰围" prop="yaowei">
    <el-select v-model="ruleForm.yaowei" placeholder="请选择腰围" style="width:100%"  filterable>
      <el-option v-for="item in yaowei" :label="item + 'cm'" :value="item"></el-option>
    </el-select>
  </el-form-item>
  </td></tr>
<tr><td>
  <el-form-item label="臀围" prop="tunwei">
    <el-select v-model="ruleForm.tunwei" placeholder="请选择臀围" style="width:100%"  filterable>
      <el-option v-for="item in tunwei" :label="item + 'cm'" :value="item"></el-option>
    </el-select>
  </el-form-item>
</td><td>
  <el-form-item label="最大卧推" prop="max_wt">
    <el-select v-model="ruleForm.max_wt" placeholder="请选择最大卧推" style="width:100%"  filterable>
      <el-option v-for="item in max_wt" :label="item + 'kg'" :value="item"></el-option>
    </el-select>
  </el-form-item>
  </td></tr>
<tr><td>
  <el-form-item label="最大硬拉" prop="max_yl">
    <el-select v-model="ruleForm.max_yl" placeholder="请选择最大硬拉" style="width:100%"  filterable>
      <el-option v-for="item in max_yl" :label="item + 'kg'" :value="item"></el-option>
    </el-select>
  </el-form-item>
</td><td>
  <el-form-item label="最大深蹲" prop="max_sd">
    <el-select v-model="ruleForm.max_sd" placeholder="请选择最大深蹲" style="width:100%"  filterable>
      <el-option v-for="item in max_sd" :label="item + 'kg'" :value="item"></el-option>
    </el-select>
  </el-form-item>
  </td></tr>
  
  <el-form-item>
    <el-button type="primary" @click="submitForm('ruleForm')">立即创建</el-button>
    <el-button @click="resetForm('ruleForm')">重置</el-button>
  </el-form-item>
</el-form>
</el-main>
</el-container>
  </div>

</template>

<script>
  export default {
    data() {
      return {
        zheight: [],
        zweight: [],
        chest:[],
        shoulder:[],
        biwei:[],
        yaowei:[],
        tunwei:[],
        max_wt:[],
        max_yl:[],
        max_sd:[],


        ruleForm: {
          name: '',
          url: '',
          height: '',
          weight: [],
          chest:[],
          shoulder:[],
          biwei:[],
          yaowei:[],
          tunwei:[],
          max_wt:[],
          max_yl:[],
          max_sd:[],
        },
        rules: {
          name: [
            { required: true, message: '请输入姓名', trigger: 'blur' },
            { min: 2, max: 10, message: '长度在 3 到 5 个字符', trigger: 'blur' },
            // { trigger:'blur',validator:this.changeTitle}
          ],
          url: [
            { required: true, message: '请输入链接地址', trigger: 'blur' },
            { type:'url', message: '必须是链接地址', trigger: 'blur' }
          ],
          // height: [
          //   { required: true, message: '请选择身高', trigger: 'blur' ,type: 'number'},
          // ],
          // weight: [
          //   { required: true, message: '请选择体重', trigger: 'blur' ,type: 'number'},
          // ],
          // chest: [
          //   { required: true, message: '请选择胸围', trigger: 'blur' ,type: 'number'},
          // ],
          // shoulder: [
          //   { required: true, message: '请选择肩宽', trigger: 'blur' ,type: 'number'},
          // ],
          // biwei: [
          //   { required: true, message: '请选择臂围', trigger: 'blur' ,type: 'number'},
          // ],
          // yaowei: [
          //   { required: true, message: '请选择腰围', trigger: 'blur' ,type: 'number'},
          // ],
          // tunwei: [
          //   { required: true, message: '请选择臀围', trigger: 'blur' ,type: 'number'},
          // ],
          // max_wt: [
          //   { required: true, message: '请选择卧推重量', trigger: 'blur' ,type: 'number'},
          // ],
          // max_yl: [
          //   { required: true, message: '请选择硬拉重量', trigger: 'blur' ,type: 'number'},
          // ],
          // max_sd: [
          //   { required: true, message: '请选择深蹲重量', trigger: 'blur' ,type: 'number'},
          // ],

        }
      };
    },
    methods: {
      changeTitle(){
        document.title = this.ruleForm.name + "个人资料填写中ing"
        return true;
      },
      getArr(start,end){
        var hei = Array();
        for (var i = start; i <= end; i++) {
          hei.push(i);
        }
        return hei;
      },
      getheight(){
        return this.getArr(155,190);
      },
      getweight(){
        return this.getArr(60,110)
      },
      getchest(){
        return this.getArr(80,125);
      },
      getshoulder(){
        return this.getArr(40,60)
      },
      getbiwei(){
        return this.getArr(35,60)
      },
      getyaowei(){
        return this.getArr(70,90)
      },
      gettunwei(){
        return this.getArr(80,110)
      },
      getmax_wt(){
        return this.getArr(70,200)
      },
      getmax_yl(){
        return this.getArr(80,300)
      },
      getmax_sd(){
        return this.getArr(70,250)
      },
      submitForm(formName) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.$http.post(gconfig.apiClient + '/zhijia/phone/test/image/merge', {name:this.ruleForm.name,img:this.ruleForm.url,height:this.ruleForm.height,weight:this.ruleForm.weight,chest:this.ruleForm.chest,shoulder:this.ruleForm.shoulder,biwei:this.ruleForm.biwei,yaowei:this.ruleForm.yaowei,tunwei:this.ruleForm.tunwei,max_wt:this.ruleForm.max_wt,max_yl:this.ruleForm.max_yl,max_sd:this.ruleForm.max_sd}, {
              headers: {

              },
              emulateJSON: true
            }).then(function (response) {
              // 这里是处理正确的回调
              // router.push('j')
              // this.$router.push({ path: '/j' })
              // this.$router.push({ path: '/u/100714'})
              
            }, function (response) {
              // 这里是处理错误的回调
              // this.$router.push({ path: '/u/100714'})
              // console.log(response)
              window.location.href='http://x.3long.cc/meger_aaa.jpg'
            })
            
            // alert('submit!');
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      },
      resetForm(formName) {
        this.$refs[formName].resetFields();
      }

    },
    mounted: function () {
        document.title ="个人资料"
        this.zheight = this.getheight();
        this.zweight = this.getweight();
        this.chest = this.getchest();
        this.shoulder = this.getshoulder();
        this.biwei = this.getbiwei();
        this.yaowei = this.getyaowei();
        this.tunwei = this.gettunwei();
        this.max_wt = this.getmax_wt();
        this.max_yl = this.getmax_yl();
        this.max_sd = this .getmax_sd();
      }
  }
</script>
<style type="text/css">
  .el-form-item__content {
    margin-right: 10px;
  }

  .content-vaglin {
    vertical-align: middle;
    line-height: 60px;
    font-size: 34px;
    font-weight: bold;
    margin-top: 20px;
  }

  .el-form-item__label {
    padding-right: 24px;
  }

  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 80px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>


