<template>
<div>
  <img :onerror="defaultImg" :src="src" :height="height" :width="width" :style="stylez">
</div>
</template>

<script type="text/javascript">
  export default{
    // props: ['srcImg','height','width','stylez'],
    props: {
      src:{
        // required: true
      },
      height: {
        required: true
      },
      width: {
        required: true
      },
      stylez: {
        default: ''
      }
    },
    data () {
      return {
        defaultImg: gconfig.defaultImg
      }
    }
  }

</script>


