  <div>
    <el-row class="div-under" style="height:50px;vertical-align:center">
      <el-col :xs="24" :sm="24" :md="24" :lg="24">
        <div class="float-l" style="vertical-align:middle;line-height:60px;">
        <el-tag v-for="tag in tags" :key="tag" size="mini" style="margin:0 3px 0 3px">{{tag}}</el-tag>
      </div>
    </el-col>
    </el-row>
  </div>
  <div class="div_height22"></div>
  <div>
    <el-row>
      <el-col :xs="4" :sm="4" :md="4" :lg="4">
        <div style="font-size:14px;color:gray">简介</div>
      </el-col>
      <el-col :xs="20" :sm="20" :md="20" :lg="20">
        <div class="text-size14 float-l" style="line-height:20px;font-size:14px;text-align:left;">{{intro}}</div>
      </el-col>
    </el-row>
  </div>
  <div class="div_height22"></div>
  <!-- <div class="div-under"></div> -->
  <div>
    <el-row>
      <el-col :xs="12" :sm="12" :md="12" :lg="12">
        <div style="height:30px;line-height:30px;" @click="divred = true" v-bind:class="[divred?'div-bottom-red':'div-bottom-gray']">氮泵</div>
      </el-col>
      <el-col :xs="12" :sm="12" :md="12" :lg="12">
        <div style="height:30px;line-height:30px;" @click="divred = false" v-bind:class="[divred?'div-bottom-gray':'div-bottom-red']">铁友</div>
      </el-col>
    </el-row>
  </div>
  <div class="div-hy" v-show="!divred">
   
    <div v-for="stu in students" :key="stu.id" style="border-bottom:solid 1px #e6e6e6;padding-top:15px;padding-bottom:15px">
      <el-row>
