          <div style="padding-left:10px;text-align:left;padding-top:5px" class="text-size12">
            <div class="text-size14"><strong>{{stu.username}}</strong></div>
            <div >{{stu.intro}}</div>
            <div class="div-under div_height10"></div>
            <div >
              <imagez :src="stu.pic" width="100%" height="auto"></imagez>
              <!-- <img :onerror="defaultImg" :src="stu.pic" width="100%" height="auto"> -->
            </div>
            
          </div>
        </el-col>
      </el-row>
    </div>
    
  </div>


  <div class="div-rc" v-show="divred">
    
    <feedcom :feeds="feeds"></feedcom>
