  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }

  
  .div-under{
    border-right: none;
    border-bottom: solid 1px #e6e6e6;
  }
  
  .float-l{
    float:left;
  }
  .float-r{
    float:right;
  }
  .el-row{
    vertical-align: center;
    /*line-height: 60px;*/
  }
  .div-pad10{
    height:50px;
    /*background-color:#99a9bf;*/
    padding-left: 20px;
    padding-right: 20px;
  }
  .div-pad40{
    padding-left:10px;
    padding-right: 10px;
  }
  .el-carousel__item h3 {
    color: #475669;
    font-size: 14px;
    opacity: 0.75;
    line-height: 80px;
    margin: 0;
  }
  
  .el-carousel__item:nth-child(2n) {
    background-color: #99a9bf;
  }
  
  .el-carousel__item:nth-child(2n+1) {
    background-color: #d3dce6;
  }
</style>


