<template>
<div>
  <div v-for="feed in feeds" :key="feed.id" >
    <div style="border-bottom:solid 1px #e6e6e6;padding-top:15px;padding-bottom:15px">
      <el-row>
        <el-col :xs="24" :sm="24" :md="24" :lg="24">
          <div style="padding-left:0px;text-align:left;padding-top:5px" class="text-size12">
            <div style="padding-left:20px;padding-right:20px">
            <div >{{feed.feed_text}}</div>
            </div>
            <div v-if="feed.feed_pic">
              <div v-if="feed.type === '1'">
                <div class="div_height15"></div>
                <center><img :onerror="defaultImg" :src="feed.feed_pic" width="80%" height="auto"></center>
              </div>
              <div v-if="feed.type === '2'">
                <div style="padding-left:20px;padding-right:20px">

                  <div class="div-link" style="background-color:#f5f5f5;height:30px;line-height:30px"><a class="div-pad10" :href="feed.feed_pic">{{feed.feed_pic}}</a>
                  </div>
                </div>
              </div>
            </div>
            
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</div>
</template>

<script type="text/javascript">
  export default{
    props: ['feeds'],
    data () {
      return {
        defaultImg: 'this.src="' + require('./../assets/404.jpg') + '"'
      }
    },
    mounted: function () {
      // console.log(this.feeds)
      
    }
  }

</script>


